.question-сontainer {
  padding-top: 0;
  padding-left: 40px;
  padding-right: 40px;
  font-family: toster-text, Helvetica, Arial, sans-serif;
}
.question-header {
  padding: 15px 0;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px #9099a3 solid;
}

.question-header__image {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
}
.question-header__image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.question-header__user-name {
  text-decoration: none;
  margin-left: 10px;
  color: black;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
}

.question-header__user-name:hover {
  text-decoration: underline;
}
.question-header__user-email {
  margin-left: 10px;
  color: #9099a3;
  font-size: 14px;
}
.question-tag {
  display: flex;
  align-items: center;
}
.question-tag__image {
  text-decoration: none;
  width: 16px;
  height: 16px;
}
.question-tag__image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.question-tag__name-tag > span {
  margin-left: 10px;
  color: #2d72d9;
  font-weight: 700;
  font-size: 16px;
}
.question-tag__name-tag > span:hover {
  text-decoration: underline;
}
.question-title {
  padding-top: 5px;
  font-weight: 700;
  font-size: 23px;
  color: #333;
}
.question-body {
  padding-top: 15px;
  padding-bottom: 15px;
}
.question-body__content {
  margin: 0;
  word-break: break-all;
  padding-bottom: 5px;
  font-size: 15px;
  line-height: 1.5em;
  color: #333333;
}
.question-body__date-of-creation {
  color: #9099a3;
  font-weight: 400;
}
.question-answers__title {
  margin: 0;
  padding: 12px 0;
  font-size: 13px;
  color: black;
  font-weight: 700;
  border-bottom: 2px solid #333;
}
.question-answers__block {
  padding-top: 28px;
}
.question-answer {
  display: flex;
  align-items: center;
}

.question-answer__image {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}
.question-answer__image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.question-answer__user-name {
  text-decoration: none;
  padding-left: 20px;
}
.question-answer__user-name > span {
  margin: 0;
  font-size: 15px;
  font-weight: 700;
  color: black;
}
.question-answer__user-name > span:hover {
  text-decoration: underline;
}
.question-answer__user-email {
  padding-left: 10px;
  color: #9099a3;
  font-size: 14px;
}
.question-answer__title {
  padding-left: 55px;
  font-size: 14px;
  color: #9099a3;
}
.question-answer__text {
  margin: 0;
  font-size: 15px;
  color: #333;
  padding-top: 15px;
  padding-bottom: 30px;
  border-bottom: 1px solid #f0f0f0;
}

.input-group {
  display: flex;
}
.input-group__image {
  margin-top: 10px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}
.input-group__image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.form-control {
  margin-top: 10px;
  margin-left: 20px;
  border: 2px #dee1e5 solid;
}
.form-control:hover {
  border: 2px #2d72d9 solid;
}
.form-control:focus {
  border: 2px #2d72d9 solid;
}
.form-control-error {
  margin-top: 10px;
  margin-left: 20px;
  border: red 1px solid;
  border-radius: 4px;
}
.form-control-error:focus {
  border: red 1px solid;
}
.form-control-error:hover {
  border: red 1px solid;
}
.form-control-error__text {
  display: flex;
  justify-content: center;
  color: red;
  font-size: 13px;
  font-weight: bold;
  padding-bottom: 20px;
}
.form-control-error__text > span {
  padding-top: 10px;
}
.btn {
  display: flex;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 40px;
  background-color: white;
  padding: 0;
  border: none;
  cursor: pointer;
}
.btn__text {
  border: 1px #64bd76 solid;
  color: #64bd76;
  padding: 10px;
}
.btn__text:hover {
  opacity: 0.5;
}
.btn .showAuthorisation {
  display: block;
}
.hideAuthorisation {
  display: none;
}
.authorisation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-top: 10px;
  padding: 0;
}
.authorisation__image {
  margin-top: 80px;
  margin-bottom: 20px;
  width: 150px;
  height: 150px;
  overflow: hidden;
  border-radius: 50%;
}
.authorisation__image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.authorisation__btn {
  text-decoration: none;
  color: #65c178;
  border: 1px #65c178 solid;
  margin-bottom: 60px;
}
.authorisation__btn-text {
  padding: 20px 20px 20px 20px;
}
.hide {
  display: none;
}
